import React from 'react';
import ReactDOM from 'react-dom';

import Home from './components/home/Home';
import Projects from './components/projects/Projects';
import Experience from './components/experience/Experience';
import Footer from './components/Footer';

// import { Button, ButtonGroup, Nav, NavItem, Navbar,   } from 'react-bootstrap';
import Resume from "./files/Amir.pdf";
import './index.css';

import { HashRouter, Route, Link } from 'react-router-dom';

const buttonGroups = (
    <div className="btn-group btn-group-justified" role="group" style={{ marginTop: "10px", marginLeft: "10px", marginRight: "10px" }} aria-label="...">
        <div className="btn-group" role="group">
            <Link to="/"><button type="button" className="btn btn-default">Home</button></Link>
        </div>
        <div className="btn-group" role="group">
            <Link to="/experience"><button type="button" className="btn btn-default">Experience</button></Link>
        </div>
        <div className="btn-group" role="group">
            <Link to="projects"><button type="button" className="btn btn-default">Projects</button></Link>
        </div>
        <div className="btn-group" role="group">
            <a href={ Resume } target="_blank" className="external" rel="noopener noreferrer"><button type="button" className="btn btn-default">Resume</button></a>
        </div>
    </div>
);

// const navbarInstance = (
//     <Nav bsStyle="tabs" activeKey={1}>
//         <NavItem eventKey={1} href="/">Home</NavItem>
//         <NavItem eventKey={2} href="/experience">Experience</NavItem>
//         <NavItem eventKey={3} href="/projects">Projects</NavItem>
//     </Nav>
// );

const routes = (
    <HashRouter>
        <div>
            <div style={{ marginBottom: "3cm" }}>
                { buttonGroups }
            </div>
            <Route path="/experience" component = { Experience }/>
            <Route path="/projects" component= { Projects }/>
            <Route exact path="/" component ={ Home }/>
            <div>
                <Footer/>
            </div>
        </div>
    </HashRouter>
);

ReactDOM.render(routes, document.getElementById('root'));




